export default {
	layout: 'search',

	async asyncData({$axios, error, query}) {
		const params = {}
		const res = await $axios.$get('/apis/spider', {params: params})
		return {tableData: res.items.slice(0, 30)}
	},

	methods: {
		goBack() {
			window.history.length > 1
		        ? this.$router.go(-1)
		        : this.$router.push('/')
		}
	}
};
